div.main-header {
    background: #000 url(./images/1.png) center 0 no-repeat;
}

div.team-header {
    background: #000 url(./images/4.png) center 0 no-repeat;
}

div.vr-header {
    background: #000 url(./images/7.png) center 0 no-repeat;
}

div.contact-header {
    background: #000 url(./images/10.png) center 0 no-repeat;
}

div.media-header {
    background: #000 url(./images/8.png) center 0 no-repeat;
}

div.privacy-header {
    background: #000 url(./images/5.png) center 0 no-repeat;
}

div.header {
    background-attachment: fixed;
    overflow: hidden;
    align-items: center;
    justify-content: center;
}

div.header-separator {
    width: 80px;
    border-top: 1px solid white;
}

div.headers {
    flex-direction: column;
    padding: 0 20px;
    align-items: center;
    justify-content: center;
}

div.header h1 {
    color: white;
    font-family: BelweSB-Medium, "Lato", sans-serif;
    font-weight: 300;
    font-size: 4rem;
    margin: 20px 0 30px 0;
}

div.header h2 {
    color: white;
    margin: 20px 0;
}

